.page_wrapper {
  padding: 65px 0px 50px 0px;
}

.blog_details_header {
  display: flex;
  gap: 10px;
  flex-direction: column;
  margin: 40px 0px 20px 0px;
}

.blog_details_header span {
  background-color: #000;
  width: max-content;
  color: #fff;
  border-radius: 5px;
  padding: 8px;
}

.blog_details_wrapper {
  width: 100%;
  max-width: 900px;
  margin: auto;
}

.blog_img_sec {
  width: 100%;
}

.blog_img_sec img {
  width: 100%;
  border-radius: 12px;
  box-shadow: var(--shadow--, 0 0 #0000), var(--shadow--, 0 0 #0000),
    var(--shadow--) !important;
}

.blog_short_desc_sec {
  margin: 20px 0px;
}

.blog_details_desc_sec {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.blog_select_box_sec {
  position: sticky;
  top: 65px;
  z-index: 99999;
  padding: 20px 0px;
  margin: 30px auto 0px;
  background: #fff;
}

.blog_select_box_sec select {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid lightgray;
  font-weight: 500;
  background-image: url("../assets/images/downarrow.webp");
  background-position: right 12px center;
  background-size: 24px 22px;
  padding-right: 36px !important;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-repeat: no-repeat;
  line-height: 20px;
}

.blog_select_box_sec select:focus-visible {
  outline: none;
}
