.calendly-badge-widget {
  left: 25px;
  width: max-content;
}

.calendly-popup .calendly-popup-content iframe #document html {
  overflow: hidden !important;
}

.calendly-badge-widget .calendly-badge-content {
  color: #000 !important;
  font-weight: 400;
  font-family: "Poppins";
}

.calendly_wrapper {
  margin: 0 auto;
}

@media (max-width: 767.98px) {
  .calendly-badge-widget {
    left: 20px;
    bottom: 35px;
  }
  .calendly_wrapper {
    margin: 30px 0px 0px 0px;
  }
}

/* spinner */
.loader {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: radial-gradient(farthest-side, #0000 94%, #0000) top/4px 4px
      no-repeat,
    conic-gradient(#0000 30%, #000000);
  -webkit-mask: radial-gradient(farthest-side, #0000 calc(100% - 4px), #000 0);
  animation: spinner-ub 0.8s infinite linear;
  margin: 50px auto;
  display: flex;
  align-items: center;
}

@keyframes spinner-ub {
  100% {
    transform: rotate(1turn);
  }
}
