.page_wrapper {
  padding: 65px 0px 50px 0px;
}

:root {
  --shadow--: 0px 2px 6px 0px rgba(60, 72, 88, 0.15) !important;
}

.blog_header {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 30px;
}

.blog_header h1 {
  font-size: 32px;
}

.blog_list_wrapper {
  margin: 30px 0px 50px 0px;
}

.blog_image_wrapper {
  width: 100%;
  max-width: 500px;
  height: 280px;
  overflow: hidden;
  border-radius: 15px;
}

.blog_image_wrapper img {
  width: 100%;
  border-radius: 15px;
  box-shadow: var(--shadow--, 0 0 #0000), var(--shadow--, 0 0 #0000),
    var(--shadow--) !important;
}

.blog_details_head {
  display: flex;
  gap: 30px;
  margin-top: 30px;
}

.blog_details_body {
  margin: 20px 0px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.blog_details_footer {
  display: flex;
  gap: 10px;
}

.blog_details_footer a {
  color: #000;
}

.blog_details_footer a:hover {
  color: #f5df4e;
}

.blog_list_wrapper .row {
  margin-bottom: 50px;
  overflow: hidden;
}

.blog_list_wrapper .row:last-child {
  margin-bottom: 0px;
}

@media (max-width: 767.98px) {
  .blog_details_head {
    margin-top: 10px;
  }
  .blog_details_body {
    margin: 0px;
  }

  .blog_list_wrapper .row {
    margin-bottom: 30px;
  }
}
